import React, { useEffect, useState } from 'react'
import './index.css'

const data = {
	children: {
		pinvathil: {
			children: {
				bhaarya: {
					title: "നേതാക്കളുടെ ഭാര്യമാർക്ക് നിയമനം",
					children: {
						mbrajesh: {
							path: 'pinvathil-bharya-mbrajesh.js',
							title: "എം ബി രാജേഷ്",
							children: {},
						},
						shamseer: {
							title :"എ.എന്‍. ഷംസീര്‍",
							path: 'pinvathil-bharya-amshamseer.js',
							children: {},
						}
					},
				}
			},
			title: "ഇടത് സർക്കാരിന്റെ പിൻവാതിൽ നിയമനം"
		},
	},
	title: "Why vote 4 BJP"
}

const Index = ({path}) => {
	const [title, setTitle] = useState('');
	const [content, setContent] = useState('');
	const [children, setChildren] = useState([]);
	useEffect(() => {
		let branch = data;
		if(path!=='index'){
			let pArr = path.split('-');
			for(let i of pArr) {
				if(i)
					branch = branch.children[i];
			}
		}
		setTitle(branch.title);
		if(branch.path)
			import('../../data/'+branch.path).then(a => setContent(a.default))
		else
			setContent(branch.content);
		let chArr = [];
		if(!branch.children){
			setChildren([]);
			return;
		}
		for(let i in branch.children){
			chArr.push({key: i, text: branch.children[i].title})
		}
		setChildren(chArr);
	}, [path])
	return (
		<div className="page">
			<div className="page-main">
			<div className="page-header">
				<div className="page-title">{title}</div>
			</div>
			<div className="page-body">
					{content && <div dangerouslySetInnerHTML={{__html:content.replace(/\n/g, '<br />')}} />}
				{/* {content} */}
				<ol>
					{children.map((i,j) => (
						<li key={j}><a href={"/?path="+(path==='index'?'':path)+"-"+i.key} key={j}>{i.text}</a></li>
					))}
				</ol>
			</div>
			</div>
		</div>
	)
}

export default Index;